export class nlClub {

    public nl_id: string;
    public nl_name: string;
    public nl_founderyear: string;
    public nl_coach: string;
    public nl_ceo: string;
    public nl_sportchef: string;
    public nl_stadion: string;
    public nl_chairs: string;
    public nl_stand: string;
    public nl_total: string;
    public nl_address: string;
    public nl_postalcode: string;
    public nl_city: string;
    public nl_email: string;
    public nl_logo: string;
    public nl_photo: string;

  
    constructor(nl_id: string, nl_name: string, nl_founderyear: string, nl_coach: string, nl_ceo: string, nl_sportchef: string, nl_stadion: string, nl_chairs: string,
                nl_stand: string, nl_address: string, nl_postalcode: string, nl_city: string, nl_email: string, nl_logo: string, nl_photo: string) {
        this.nl_id = nl_id;
        this.nl_name = nl_name;
        this.nl_founderyear = nl_founderyear;
        this.nl_coach = nl_coach;
        this.nl_ceo = nl_ceo;
        this.nl_sportchef = nl_sportchef;
        this.nl_stadion = nl_stadion;
        this.nl_chairs = nl_chairs;
        this.nl_stand = nl_stand;
        this.nl_total = nl_chairs + nl_stand;
        this.nl_address = nl_address;
        this.nl_postalcode = nl_postalcode;
        this.nl_city = nl_city;
        this.nl_email = nl_email;
        this.nl_logo = "assets/nl_assets/" + nl_logo;
        this.nl_photo = nl_photo;
    }
  }